.slides{display:grid}.slides>.slide{grid-area:1/-1}.slides>button{appearance:none;background:rgba(0,0,0,0);border:none;color:#f7b100;position:absolute;font-size:5rem;width:5rem;height:5rem;top:30%;transition:opacity .3s;opacity:.7;z-index:5}.slides>button:hover{opacity:1}.slides>button:focus{outline:none}.slides>button:first-child{left:-50%}.slides>button:last-child{right:-50%}.slideContent{width:28vw;height:35vw;background-size:cover;background-position:center center;background-repeat:no-repeat;transition:transform .5s ease-in-out;opacity:.7;display:grid;align-content:center;transform-style:preserve-3d;transform:perspective(1600px) translateX(calc(100% * var(--offset))) rotateY(calc(-42deg * var(--dir)))}.slideContentInner{transform-style:preserve-3d;transform:translateZ(2rem);transition:opacity .3s linear;text-shadow:0 .5rem 1rem #000;opacity:0}.slideContentInner .slideSubtitle,.slideContentInner .slideTitle{font-size:2rem;font-weight:normal;letter-spacing:.2ch;text-transform:uppercase;margin:0}.slideContentInner .slideSubtitle::before{content:"â€” "}.slideContentInner .slideDescription{margin:0;font-size:.8rem;letter-spacing:.2ch}.slide[data-active]{z-index:2;pointer-events:auto}.slide[data-active] .slideBackground{opacity:.2;transform:none}.slide[data-active] .slideContentInner{opacity:1}.slide[data-active] .slideContent{--x: calc(var(--px) - 0.8);--y: calc(var(--py) - 0.8);opacity:1;transform:perspective(1000px)}.slide[data-active] .slideContent:hover{transition:none;transform:perspective(1000px) rotateY(calc(var(--x) * 45deg)) rotateX(calc(var(--y) * -45deg))}@media only screen and (min-width: 320px)and (max-width: 480px){.slideContent{height:65vh;width:70vw}.slides>button{width:13rem;height:10rem}.slideContentInner .slideSubtitle,.slideContentInner .slideTitle{font-size:14pt}.slideContentInner .slideDescription{font-size:11pt}}